@import url(https://fonts.googleapis.com/css?family=Roboto:100,400&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  background: repeating-linear-gradient(to right, #082840, #07304e, #082840);
  background-image: url(/crispy-nugget/static/media/mars-globe-valles-marineris-enhanced.1862d0d4.jpg);
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 5vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.circle {
  background: repeating-radial-gradient(circle, #0f64a2, #1070b7);
  width: 60vmin;
  height: 60vmin;
  border-radius: 30vmin;
  font-family: "Roboto";
}

.box {
  border: 10px solid white;
  width: 45vmin;
  height: 40vmin;
  padding: 5vmin 0 0;

  font-size: calc(17vmin);
  line-height: calc(18vmin);

  font-family: "Roboto", sans-serif;
  font-weight: 100;
  box-shadow: 3px 3px 1px 0px black;
}

.text-highlight {
  color: #f3289d;

}
.text-mini {
  font-size: calc(8px + 2vmin);
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

